import React, { Component } from 'react';

import FormInput from '../form-input/form-input.component';
import CustomButton from '../custom-button/custom-button.component';

import { auth, createUserProfileDocument } from '../../firebase/firebase.utils';

import './sign-up.styles.scss';

class SignUp extends Component {
    constructor(props) {
        super(props);

        this.state = {
            displayName: '',
            email: '',
            password: '',
            confirmPassword: ''
        }
    }

    handleChange = (e) => {
        // destructure name and value from target 
        const { name, value } = e.target;
        // dynamically set state of name to value
        this.setState({
            [name]: value
        });
    }

    handleSubmit = async (e) => {
        // prevent page from refreshing upon submission
        e.preventDefault();

        // destructure input
        const { displayName, email, password, confirmPassword } = this.state;
        
        // validate input
        if (password !== confirmPassword) {
            alert('passwords dont match');
            return;
        }

        try {
            // destructure the user from the firebase function which will create a new account
            const { user } = await auth.createUserWithEmailAndPassword(email, password);

            // call createUserProfileDocument passing in the user, and the DN as an object
            await createUserProfileDocument(user, { displayName });

            // reset state to initial state
            this.setState({
                diplayName: '',
                email: '',
                password: '',
                confirmPassword: ''
            })

        } catch (error) {
            console.log('error creating user', error);
        }
    }

    render() {
        const { displayName, email, password, confirmPassword } = this.state;
        return (
            <div className="sign-up">
                <h2 className="title">I do not have an account</h2>
                <span>Sign up with your email</span>

                <form className="sign-up-form"  onSubmit={this.handleSubmit}>
                    <FormInput 
                        name="displayName" 
                        type="text" 
                        value={displayName} 
                        handleChange={this.handleChange}
                        label="Display Name"
                        required /> 
                    <FormInput 
                        name="email" 
                        type="email" 
                        value={email} 
                        handleChange={this.handleChange}
                        label="Email"
                        required />
                    <FormInput 
                        name="password" 
                        type="password" 
                        value={password} 
                        handleChange={this.handleChange}
                        label="Password"
                        required />
                    <FormInput 
                        name="confirmPassword" 
                        type="password" 
                        value={confirmPassword} 
                        handleChange={this.handleChange}
                        label="Confirm Password"
                        required />
                    
                    <CustomButton type='submit'>SIGN UP</CustomButton> 
                </form>
            </div>
        )
    }
}

export default SignUp;